@import '../css/settings';
@import '../css/mixins/index';

.dropdown {
    @include stackable();

    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-transition: all .2s ease;
    border: ($grid * .2) solid $border-color;
    border-radius: $border-radius;
    box-sizing: border-box;
    clear: both;
    display: block;
    height: ($grid * 10.2);
    margin-bottom: ($grid * -.2);
    position: relative;
    text-align: left;
    top: ($grid * -.2);
    transition: all .2s ease;

    &:after {
        background: url('data:image/svg+xml;base64,');
        content: '';
        display: block;
        height: ($grid * 2.8);
        position: absolute;
        right: ($grid * 3.4);
        top: ($grid * 3.4);
        width: ($grid * 2.8);
    }

    &.is-hidden {
        display: none;
    }

    &:focus {
        outline: none;
    }

    &:hover {
        border-color: map-get($colors, blue-hover);
        z-index: 10;
    }

    &.is-focused {
        border-color: map-get($colors, blue-focus);
        box-shadow: 0 0 ($grid * .8) map-get($colors, blue-shadow);
        z-index: 20;
    }

    &.is-error {
        background-color: #fff8f7;
        border-color: map-get($colors, error-border);
        box-shadow: none;
        z-index: 5;

        &:hover {
            border-color: map-get($colors, error-hover);
            z-index: 10;
        }

        &.is-focused {
            border-color: map-get($colors, error);
            box-shadow: 0 0 ($grid * .8) map-get($colors, error-shadow);
            z-index: 20;
        }
    }

    &.is-warning {
        background-color: #fffaf0;
        border-color: map-get($colors, warning-border);
        box-shadow: none;
        z-index: 5;

        &:hover {
            border-color: map-get($colors, warning-hover);
            z-index: 10;
        }

        &.is-focused {
            border-color: map-get($colors, warning-hover);
            box-shadow: 0 0 ($grid * .8) map-get($colors, warning-border);
            z-index: 20;
        }
    }

    &.is-disabled {
        background-color: #fafafa;
        border-color: map-get($colors, grey-lines);

        &:hover {
            border-color: map-get($colors, grey-lines);
        }
    }

    &.is-loading {
        &:after {
            @include button__loader;
            content: '';
            margin-top: -10px;
            position: absolute;
            right: 15px;
            top: 50%;
        }
    }
}

.dropdown__label {
    @include typography(($grid * 2.8), regular);

    color: map-get($colors, 'grey-text');
    line-height: ($grid * 10);
    padding: 0 ($grid * 4);
    position: absolute;
    transition: all .2s ease;
    white-space: nowrap;

    .is-selected & {
        @include typography(($grid * 2.2), regular);

        transform: translate(0, ($grid * -2));
        -ms-transform: translate(0, ($grid * -2));

        .is-error &,
        .is-warning & {
            @include typography(($grid * 2.2), regular);

            left: 0;
            top: 0;
            transform: translate(0, ($grid * -2));
            -ms-transform: translate(0, ($grid * -2));
        }
    }

    .is-error & {
        color: map-get($colors, error);
    }

    .is-warning & {
        color: map-get($colors, warning);
    }

    .is-disabled & {
        color: map-get($colors, grey-lines);
    }
}

.dropdown__current-option {
    @include typography(($grid * 2.8), semi-bold);

    color: map-get($colors, black);
    line-height: ($grid * 10);
    padding: 0 ($grid * 4);
    position: absolute;

    .is-selected &,
    .is-error &,
    .is-warning & {
        height: ($grid * 9);
        top: ($grid * 1.2);
    }

    .is-disabled & {
        color: map-get($colors, grey-lines);
    }
}

.dropdown__select {
    @include opacity(0);

    appearance: none;
    cursor: pointer;
    height: ($grid * 10.2);
    outline: none;
    position: absolute;
    width: 100%;
    z-index: 2;
}



