@import '../css/settings';
@import '../css/mixins/index';

.radio {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-transition: all .2s ease;
    border: ($grid * .2) solid $border-color;
    border-radius: $border-radius;
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    margin-bottom: ($grid * .6);
    overflow: hidden;
    padding: 0;
    position: relative;
    transition: all .2s ease;

    &.borderless {
        border: 0;
        padding-bottom: $grid * .2;
        padding-top: $grid * .2;
    }

    &.is-disabled {
        background: #fafafa;
        color: map-get($colors, grey-lines);
        cursor: default;
        pointer-events: none;

        &:hover {
            border-color: $border-color;
        }

        &::after {
            @include opacity(.2);
        }
    }
}

.radio__option {
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-transition: all .2s ease;
    -webkit-user-select: none;
    background: white;
    overflow: hidden;
    padding-bottom: ($grid * 2.2);
    padding-top: ($grid * 1.8);
    position: relative;
    transition: all .2s ease;

    .borderless & {
        border-radius: $border-radius;

        &:first-child {
            padding-top: ($grid * 1.8);
        }

        &:last-child {
            padding-bottom: ($grid * 2.2);
        }
    }

    &:hover {
        background: map-get($colors, block-hover);

        .is-disabled & {
            &::before {
                border-color: $border-color;
            }
        }
    }

    &:first-child {
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;
        padding-top: ($grid * 2.8);
    }

    &:last-child {
        border-bottom-left-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
        padding-bottom: ($grid * 3.2);
    }
    &::before {
        background: white;
        border: ($grid * .2) solid $border-color;
        border-radius: ($grid * 2.8);
        box-sizing: border-box;
        content: '';
        display: block;
        height: ($grid * 2.8);
        left: ($grid * 4);
        position: absolute;
        top: ($grid * 2.4);
        -webkit-transition: all .2s ease;
        transition: all .2s ease;
        width: ($grid * 2.8);
    }

    &::after {
        @include opacity(0);

        background: white;
        border-radius: ($grid * 1.2);
        content: '';
        display: block;
        height: ($grid * .8);
        left: ($grid * 5);
        position: absolute;
        top: ($grid * 3.4);
        transform: scale(3, 3);
        -ms-transform: scale(3, 3);
        -webkit-transition: all .2s ease;
        transition: all .2s ease;
        width: ($grid * .8);
    }

    &:first-child {
        &::before {
            top: ($grid * 3.4);
        }

        &::after {
            top: ($grid * 4.4);
        }
    }

    .dynamic-styling & {
        &:after {
            display: block;
            z-index: 10;
        }

        &:before {
            display: block;
            z-index: 10;
        }
    }

    &.is-selected {
        &::before {
            background: map-get($colors, klarna-blue);
            border-color: map-get($colors, klarna-blue);
        }

        &::after {
            @include opacity(1);

            top: ($grid * 3.4);
            transform: scale(1, 1);
            -ms-transform: scale(1, 1);
        }

        &:first-child {
            &::after {
                top: ($grid * 4.4);
            }
        }

        .dynamic-styling & {
            &:before, &:after {
                opacity: 0;
            }
        }

        .is-disabled & {
            &::before {
                background: map-get($colors, grey-lines);
                border-color: map-get($colors, grey-lines);
            }

            &::after {
                @include opacity(1);

                top: ($grid * 3.4);
            }

            &:first-child::after {
                @include opacity(1);

                top: ($grid * 4.4);
            }
        }
    }

    .borderless & {
        &:first-child {
            &::before {
                top: ($grid * 2.4);
            }

            &::after {
                top: ($grid * 3.4);
            }
        }
    }

    padding-left: ($grid * 10);
    padding-right: ($grid * 4);
}

.radio__option__heading {
    @include typography(($grid * 2.8), semi-bold);
    display: block;
    line-height: ($grid * 4);
}

.radio__option__description {
    @include typography(($grid * 2.6), regular);
    color: map-get($colors, grey-text);
    line-height: ($grid * 4);

    .is-disabled & {
        color: map-get($colors, grey-lines);
    }
}



